.section-slider {
  &__images {
    z-index: 5; }
  &__caption {
    z-index: 10;
    position: absolute;
    transform: translateY(-100%);
    width: 600px;
    background: white; }
  &__nav {
    z-index: 10;
    display: flex;
    justify-content: flex-end;
    flex-direction: row-reverse;
    position: absolute;
    width: 600px;
    transform: translate(600px, -100%); } }
