.aspectratio-16-9 {
  float: left;
  display: block;
  padding-top: 56.25%;
  width: 0;
}

.aspectratio-4-3 {
  float: left;
  display: block;
  padding-top: 133.33333%;
  width: 0;
}

.aspectratio-3-2 {
  float: left;
  display: block;
  padding-top: 66.66667%;
  width: 0;
