}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

*::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

@media (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

@media (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 37.5em) {
  html {
    font-size: 30%;
  }
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

::selection {
  background-color: #3E3B4B;
  color: #fff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

body {
  font-size: 1.6rem;
  line-height: 1.6;
  font-family: europa, sans-serif;
  font-weight: 400;
}

.heading-primary {
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 6rem;
  line-height: 1;
  color: #252235;
}

.heading-secondary {
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 3.5rem;
  line-height: 1;
  color: #252235;
}

.heading-tertiary {
  font-size: 1.6rem;
}

.paragraph {
  font-size: 1.6rem;
}

.navigation-footer__item {
  list-style: none;
}

.page-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 1rem 0;
}

.navigation-mobile {
  display: none;
}

@media (max-width: 37.5em) {
  .navigation-mobile {
    display: block;
  }
}

.navigation-primary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 37.5em) {
  .navigation-primary {
    display: none;
  }
}

.navigation-primary__item {
  margin-right: 2rem;
}

.navigation-primary__cta {
  margin: 0 4rem;
}

.logo__image {
  height: 40px;
}

.section-featured {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.section-featured .featured-image {
  position: absolute;
}

.section-featured--reversed {
  margin-bottom: 1.4rem;
}

.section-featured--reversed .section-featured__content {
  grid-row: 1;
}

.section-banner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 10em;
}

.section-banner__image {
  overflow: hidden;
  z-index: 5;
  grid-row: 1/2;
  grid-column: 1/3;
}

.section-banner__content {
  z-index: 10;
  grid-row: 1/2;
  grid-column: 1/2;
}

.page-footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #252235;
  background-image: url("../../assets/footer-bg.png");
  background-repeat: repeat;
  background-size: 40%;
}

.section-featured__media--divided {
  position: relative;
}

.section-featured__media--divided:before, .section-featured__media--divided:after {
  content: "";
  background: white;
  position: absolute;
  z-index: 10;
}

.section-featured__media--divided:before {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1.4rem;
  height: 100%;
}

.section-featured__media--divided:after {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 50%;
  height: 1.4rem;
}

.has-hover {
  position: relative;
}

.has-hover:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  top: 110%;
  left: 0;
  background: #252235;
  transition: transform 0.5s ease-in;
  transform: scaleX(0);
  transform-origin: right;
}

.has-hover:hover:after {
  transform: scale(1);
  transform-origin: left;
}

.section-slider__images {
  z-index: 5;
}

.section-slider__caption {
  z-index: 10;
  position: absolute;
  transform: translateY(-100%);
  width: 600px;
  background: white;
}

.section-slider__nav {
  z-index: 10;
  display: flex;
  justify-content: flex-end;
  flex-direction: row-reverse;
  position: absolute;
  width: 600px;
  transform: translate(600px, -100%);
}
